import React, { useState, useEffect } from 'react';
const STORAGE_KEY = 'myapp_stagedItems';

export default function StageView({
  stagedItems,
  setStagedItems,
  restoreLastStageAndInventory,
  restorePriorAskCountOnly
}) {
  const [editing, setEditing] = useState({ idx: null, field: null, value: '' });
  const chunkSize = 15;
  const [showAsk, setShowAsk] = useState(true);

  // Pagination: split stagedItems into chunks of 15
  const chunks = [];
  for (let i = 0; i < stagedItems.length; i += chunkSize) {
    chunks.push(stagedItems.slice(i, i + chunkSize));
  }

  function handleEditInit(idx, field, value) {
    setEditing({ idx, field, value });
  }

  function handleEditChange(e) {
    setEditing((cur) => ({ ...cur, value: e.target.value }));
  }

  function handleEditCommit() {
    if (editing.idx == null || !editing.field) return;
    const newItems = [...stagedItems];
    let val = editing.value;
    if (editing.field === 'unitPrice') {
      // Format to 2 decimals
      val = (val === '' ? '' : Number(val).toFixed(2));
    }
    newItems[editing.idx] = {
      ...newItems[editing.idx],
      [editing.field]: val
    };
    setStagedItems(newItems);
    setEditing({ idx: null, field: null, value: '' }); // exit edit mode
  }

  function handleEditKeyDown(e) {
    if (e.key === 'Enter') {
      handleEditCommit();
    } else if (e.key === 'Escape') {
      setEditing({ idx: null, field: null, value: '' });
    }
  }

  function handleEditBlur() {
    handleEditCommit();
  }

  function handleAskChange(idx, value) {
    const newItems = [...stagedItems];
    newItems[idx].askCount = Number(value) || 0;
    setStagedItems(newItems);
  }

  function handleAskFocus(e) {
    if (e.target.value === '0') e.target.value = '';
  }

  // Save local
  useEffect(() => {
    try {
      localStorage.setItem(STORAGE_KEY, JSON.stringify(stagedItems));
    } catch {}
  }, [stagedItems]);

  // UI
  return (
    <div>
      <div className="mb-3">
        <button
          className="btn btn-secondary me-2"
          onClick={() => setShowAsk((v) => !v)}
        >
          Toggle Ask Qty
        </button>
        <button
          className="btn btn-primary me-2"
          onClick={restoreLastStageAndInventory}
        >
          Restore Last Stage
        </button>
        <button
          className="btn btn-warning"
          onClick={restorePriorAskCountOnly}
        >
          Restore Ask Count from Prior Stage
        </button>
      </div>
      {chunks.length === 0 && <div>No items staged.</div>}
      {chunks.map((rows, pageIdx) => (
        <table className="table table-bordered" key={pageIdx}>
          <thead>
            <tr>
              <th>Product</th>
              <th>Price ($)</th>
              <th>Qty</th>
              {showAsk && <th>Ask</th>}
            </tr>
          </thead>
          <tbody>
            {rows.map((item, idx) => {
              const globalIdx = pageIdx * chunkSize + idx;
              return (
                <tr key={globalIdx}>
                  <td
                    onClick={() =>
                      setEditing({ idx: globalIdx, field: 'product', value: item.product })
                    }
                  >
                    {editing.idx === globalIdx && editing.field === 'product' ? (
                      <input
                        type="text"
                        className="form-control"
                        autoFocus
                        value={editing.value}
                        onChange={handleEditChange}
                        onBlur={handleEditBlur}
                        onKeyDown={handleEditKeyDown}
                        style={{ width: '100%' }}
                        placeholder="Product name"
                      />
                    ) : (
                      item.product
                    )}
                  </td>
                  <td
                    onClick={() =>
                      setEditing({ idx: globalIdx, field: 'unitPrice', value: item.unitPrice })
                    }
                  >
                    {editing.idx === globalIdx && editing.field === 'unitPrice' ? (
                      <input
                        type="number"
                        className="form-control"
                        autoFocus
                        value={editing.value}
                        onChange={handleEditChange}
                        onBlur={handleEditBlur}
                        onKeyDown={handleEditKeyDown}
                        style={{ width: 100 }}
                        step="0.01"
                        min="0"
                        placeholder="Price"
                      />
                    ) : (
                      item.unitPrice
                    )}
                  </td>
                  <td>{item.count}</td>
                  {showAsk && (
                    <td>
                      <input
                        type="number"
                        className="form-control"
                        value={item.askCount || ''}
                        onChange={e => handleAskChange(globalIdx, e.target.value)}
                        onFocus={handleAskFocus}
                        style={{ width: 70 }}
                        placeholder="Ask"
                        min="0"
                      />
                    </td>
                  )}
                </tr>
              );
            })}
          </tbody>
        </table>
      ))}
    </div>
  );
}
